import { Component, OnInit, AfterViewInit, Input, OnDestroy, Inject } from '@angular/core';
import * as Chart from 'chart.js';
import * as ChartDataLabels from 'chartjs-plugin-datalabels';
import { Observable, Subject, BehaviorSubject} from 'rxjs';
import { Subscription} from 'rxjs';
import { RobotStateService } from './../services/robot-state.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
declare var $: any;

@Component({
  selector: 'app-bardialog',
  templateUrl: './bardialog.component.html',
  styleUrls: ['./bardialog.component.css']
})
export class BarDialogComponent{
  constructor(public dialogRef: MatDialogRef<BarChartComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              public robotService: RobotStateService) {

  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}

@Component({
  selector: 'app-bar-chart',
  templateUrl: './bar-chart.component.html',
  styleUrls: ['./bar-chart.component.css']
})
export class BarChartComponent implements OnInit,AfterViewInit, OnDestroy {

  @Input() idPlot: string;
  @Input() label: string;

  canvas: any;
  ctx: any;
  private data : any;
  private myChart: any;
  private isResponsive: boolean;
  private robotService: RobotStateService;
  private map: Map<string, number>;
  private subPlotAddDatamsg : Subscription;
  private subPlotAddmsg : Subscription;
  private subPlotClearmsg : Subscription;
  public topics = ["motor_position","link_position","motor_velocity","link_velocity",
  "effort","stiffness","damping","pos_ref","vel_ref","eff_ref","aux","temperature"];
  public currentTopic :string;
  private timeout;
  private nselected=-1;
  private isAbsolute = false;
  private showLabel = false;
  private showBarValue = false;
  private skip = false;

  private mapTopicLimit: Map<string, string> = new Map<string, string>();
  private mapTopicReference: Map<string, string> = new Map<string, string>();

  private ulimvec;
  private llimvec;
  private limitHidden = [false, false];
  private barTypes = ["bar","horizontalBar"];
  private barTypeSelected = "bar";

  private chartColors = {
    red: 'rgb(255, 99, 132)',
    orange: 'rgb(255, 159, 64)',
    yellow: 'rgb(255, 205, 86)',
    green: 'rgb(75, 192, 192)',
    blue: 'rgb(54, 162, 235)',
    purple: 'rgb(153, 102, 255)',
    grey: 'rgb(201, 203, 207)'
  };

  private configChart : Chart.ChartConfiguration;

  private configChartHoriz: Chart.ChartConfiguration;

  constructor(robotService: RobotStateService,public dialog: MatDialog) {
    this.robotService = robotService;
    this.currentTopic = "";
    this.mapTopicLimit.set("link_position","poslim");
    this.mapTopicLimit.set("motor_position","poslim");
    this.mapTopicLimit.set("pos_ref","poslim");
    this.mapTopicLimit.set("link_velocity","vellim");
    this.mapTopicLimit.set("motor_velocity","vellim");
    this.mapTopicLimit.set("vel_ref","vellim");
    this.mapTopicLimit.set("effort","efflim");
    this.mapTopicLimit.set("eff_ref","efflim");
    this.mapTopicLimit.set("temperature","templim");
    this.mapTopicLimit.set("aux","auxlim");

    this.mapTopicReference.set("link_position","pos_ref");
    this.mapTopicReference.set("motor_position","pos_ref");
    this.mapTopicReference.set("link_velocity","vel_ref");
    this.mapTopicReference.set("motor_velocity","vel_ref");
    this.mapTopicReference.set("effort","eff_ref");

    if( this.label == null) this.label = "Plotter";
    this.data = {
      labels: [],
      datasets: []
    };
    
    Chart.plugins.unregister(ChartDataLabels);
  }

  setTopic(topic){
    this.robotService.currentTopicBar = topic;
  }

  getId(){
    return "myBarChart"+this.idPlot;
  }

  ngOnInit() {
    console.log("ID ONINIT "+this.getId());
  }

  ngOnDestroy() {

    clearTimeout(this.timeout);
    if(this.subPlotAddDatamsg != null)
      this.subPlotAddDatamsg.unsubscribe();
    //this.subPlotAddmsg.unsubscribe();
    //this.subPlotClearmsg.unsubscribe();
    this.robotService = null;
    this.map = null;
    this.myChart.destroy();
    window.removeEventListener('resize', this.OnWindowResize);
     //REMOVE HOSTLISTENER
     //REMOVE SETINTERVAL
   }
    
  updateBarType(event){
    this.skip = true;
    this.myChart.clear();  
    this.myChart.destroy(); 
    this.myChart = null; 
    if (this.barTypeSelected == "bar")
      this.myChart = new Chart(this.ctx, this.configChart);  
    else
      this.myChart = new Chart(this.ctx, this.configChartHoriz);  
 
    this.myChart.resize(); 
    this.nselected = -1;
    this.clearData();
    this.skip = false;
  }

  updateLabel(event){
    this.configChart.options.scales["xAxes"][0].display = event.checked;
    this.configChartHoriz.options.scales["yAxes"][0].display = event.checked;
  }

  ngAfterViewInit() {

    this.timeout = setTimeout(()=>{ 
      console.log("CHART ID "+this.idPlot);
      this.configChart = {
        plugins: [ChartDataLabels],
        type: "bar",
        data: this.data,
        options: {
          plugins: {
            datalabels: {
              formatter: function(value, context) {
                if (value == 0) return "";
                if (context.datasetIndex == 1 || context.datasetIndex == 2 ) return "";
                return Math.round(value * 100) / 100
              },
              display: (context)=>{                 
                return  this.showBarValue; 
              }
            }            
          },
          events: ['click'],          
          responsiveAnimationDuration:0,
          animation:{
            duration: 0
          },
          responsive: this.isResponsive,
          maintainAspectRatio: false,
          //cubicInterpolationMode: "",
          title: {
            display: true,
            text: this.label
          },
          tooltips: {
            enabled: true,
            mode: 'index',
            intersect: true,
            callbacks: {
              afterLabel: (tooltipItem, data)=>{ 
                this.robotService.selectJointSensorName = tooltipItem.xLabel.toString();
                this.robotService.isJoint = true;
                this.robotService.advertiseSelectedJoint(this.robotService.selectJointSensorName);
                return "";
              }
            }
          },
          /*hover: {
            mode: 'nearest',
            intersect: true
          },*/
          //scaleBeginAtZero: false,
          scales: {
            xAxes: [{
              stacked: true,
              display: this.showLabel,
              ticks: {
                //fontSize: 8,
                //lineHeight:0.5,
                //padding: 1
            },
              scaleLabel: {
                display: false,
                labelString: 'Joints'
              }
            }],
            yAxes: [{
              //stacked: true,
              display: true,
              ticks: {
                //stepSize: 0.5
                beginAtZero: true,
              },
              scaleLabel: {
                display: true,
                //labelString: 'Value'
              }
            }]
          }
        }
      };
      this.configChartHoriz =  {
        plugins: [ChartDataLabels],
        type: "horizontalBar",
        data: this.data,
        options: {
          plugins: {
            datalabels: {
              formatter: function(value, context) {
                if (value == 0) return "";
                if (context.datasetIndex == 1 || context.datasetIndex == 2 ) return "";
                return Math.round(value * 100) / 100
              },
              display: (context)=>{                 
                return  this.showBarValue; 
              }
            }            
          },
          events: ['click'],          
          responsiveAnimationDuration:0,
          animation:{
            duration: 0
          },
          responsive: this.isResponsive,
          maintainAspectRatio: false,
          //cubicInterpolationMode: "",
          title: {
            display: true,
            text: this.label
          },
          tooltips: {
            enabled: true,
            mode: 'index',
            intersect: true,
            callbacks: {
              afterLabel: (tooltipItem, data)=>{ 
                this.robotService.selectJointSensorName = tooltipItem.yLabel.toString();
                this.robotService.isJoint = true;
                this.robotService.advertiseSelectedJoint(this.robotService.selectJointSensorName);
                return "";
              }
            }
          },
          /*hover: {
            mode: 'nearest',
            intersect: true
          },*/
          //scaleBeginAtZero: false,
          scales: {
            xAxes: [{
              //stacked: true,
              display: true,
              ticks: {
                //fontSize: 8,
                //lineHeight:0.5,
                //padding: 1
            },
              scaleLabel: {
                display: false,
                labelString: 'Joints'
              }
            }],
            yAxes: [{
              stacked: true,
              display: this.showLabel,
              ticks: {
                //stepSize: 0.5
                beginAtZero: true,
              },
              scaleLabel: {
                display: true,
                //labelString: 'Value'
              }
            }]
          }
        }
      };
      this.isResponsive = true;
      this.canvas = document.getElementById(this.getId());
      this.ctx = this.canvas.getContext('2d');
      this.myChart = new Chart(this.ctx, this.configChart);     
      this.robotService.registerBarChartComponent(parseInt(this.idPlot));
      this.subPlotAddDatamsg = this.robotService.currentBarAddDatamsg.get(parseInt(this.idPlot)).subscribe(msg => {		
        
        if(msg == null)return;
        if (this.skip) return;
        if (msg != null){
          //console.log(msg);
          var topicname = msg["topic"];
          var robot = msg["robot"];
          var selected = msg["selected"];
          if(robot == null) return;
          //console.log(robot);
          //var joint = robot["joint_name"];
          var joint = new Array<any>();
          //var allJoint = robot["joint_name"];
          for (let entry of selected) { 
            var isj = entry["isJoint"]; 
            if (isj!= null && isj==true)            
              joint.push(entry["type"]);
          }
                     
          //var keys =  Object.keys(robot);
          //this.topics = keys;

          if (this.nselected != joint.length){
            if (this.data.datasets.length != 0){
              this.limitHidden[0] = !this.myChart.isDatasetVisible(1);
              this.limitHidden[1] = !this.myChart.isDatasetVisible(2);
            }
            this.clearData();
            this.addDatasets(joint);
          }

          if(this.nselected != joint.length || this.currentTopic != topicname){
            this.currentTopic = topicname;
            var limitType = this.mapTopicLimit.get(topicname);
            for (var i = 0; i < joint.length; i++) {            
              var obj = this.robotService.limits.get(joint[i]);
              if (obj != null) {
                var lim = obj[limitType];
                if (lim != null){
                  this.ulimvec[i] = lim.ulim;
                  this.llimvec[i] = lim.llim;
                } 
                else
                {
                  this.ulimvec[i] = 0;
                  this.llimvec[i] = 0;
                }              
              }
            }
          this.updateLimits(this.ulimvec,this.llimvec);
          this.nselected = joint.length;
          
            if (this.mapTopicReference.get(topicname)!= null){
              if (this.data.datasets[3]!= null) this.data.datasets.pop();
              this.addDataset(joint,"reference",null);
            }else{
              if (this.data.datasets[3]!= null) this.data.datasets.pop();
              this.myChart.update(0);
            }
          }
          
          this.setLabel(topicname);
          this.addDataToDataset(topicname);

          if (this.data.datasets[3]!=null && this.data.datasets[3].length != 0){
            let j = 0;
            for (let entry of this.data.labels) { 
              var obj = this.robotService.getJointObj(entry);
              var tmp = obj[this.mapTopicReference.get(topicname)];
              if (this.isAbsolute) tmp = Math.abs(tmp);
              this.data.datasets[3].data[j]= tmp;
              j++;
            }
            this.myChart.update(0);
          }
        }

      });
      window.addEventListener('resize', this.OnWindowResize);
      this.myChart.resize();
    },100);
    
  }

  OnWindowResize = EventListener => {
    this.myChart.resize();
  }

  clearData(){
    this.data.labels = [];
    this.data.datasets = [];
    this.robotService.clearPlot(parseInt(this.idPlot));
    this.myChart.update(0);
  }

  addDataset(msg,label,backcolor){
    var ol = Object.keys(this.chartColors);
    var colorName = ol[this.data.datasets.length % ol.length];
    var data = {
      label: label,
      backgroundColor: backcolor != null ? backcolor: this.chartColors[colorName],
      borderColor: backcolor != null ? backcolor: this.chartColors[colorName],
      borderWidth: 1,
      data: new Array(msg.length)}; //msg["value"]
    this.data.labels = msg;    
    this.data.datasets.push(data); 
    console.log(this.chartColors[colorName]);
  }

  addDatasets(msg){
      this.addDataset(msg,"",'rgb(255, 99, 132,0.5)');
      this.addDataset(msg,"LLimit",'rgba(255, 179, 179,0.5)');
      this.addDataset(msg,"ULimit",'rgba(255, 179, 179,0.5)');
      this.ulimvec = new Array(msg.length);
      this.llimvec = new Array(msg.length);      
  }

  setLabel(label){
    this.data.datasets[0].label = label;
  }

  setMinScale(val){
    this.myChart.options.scales.yAxes[0].ticks.min = parseFloat(val);
  }

  setMaxScale(val){
    this.myChart.options.scales.yAxes[0].ticks.max = parseFloat(val);
  }

  addDataToDataset(topicname){
    let i = 0;
    for (let entry of this.data.labels) { 
      var obj = this.robotService.getJointObj(entry);
      var tmp = obj[topicname];
      if (this.isAbsolute) tmp = Math.abs(tmp);
      this.data.datasets[0].data[i]= tmp;
      i++;
    }
    this.myChart.update(0);
  }

  updateLimits(ulim, llim){

    for (let i = 0; i < this.data.labels.length; i++) {
      this.data.datasets[1].data[i] = llim[i];
      this.data.datasets[2].data[i] = ulim[i];
    }
    //this.myChart.update(0);
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(BarDialogComponent, {
      width: '40%',
      height: '30%',
      data: this
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      
    });
  }
}
