import { MatDialog } from '@angular/material';
import { RobotStateService } from './../services/robot-state.service';
import { Component, OnInit } from '@angular/core';
import { PlotterComponent } from './../plotter/plotter.component';
import { CanvasComponent } from './../canvas/canvas.component';
import { PluginListComponent } from './../plugin-list/plugin-list.component';
import { ControlPanelComponent } from './../control-panel/control-panel.component';
import { TreePanelComponent } from './../tree-panel/tree-panel.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  public robotService: RobotStateService;
  constructor(robotService: RobotStateService,public dialog: MatDialog ){
    this.robotService =  robotService;
  }

  ngOnInit() {
  }

  countMainContentView(param){
    var ret;
    if (param)
      if (this.robotService.viewCounter == 0) ret=80; else ret =80/2;
    else
      if (this.robotService.viewCounter == 0) ret=0; else ret =80/2;
    return ret;
  }
}
