import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { RobotStateService } from './../services/robot-state.service';
import { Observable, Subject, Subscription } from 'rxjs';
import { AppError } from './../common/app-error';
import { NotFoundError } from './../common/not-foud-error';
import { HttpClient } from '@angular/common/http';
import { HttpService } from './../services/http.service';
import {MatPaginator, MatSort, MatTableDataSource, MatSortable, MatTable} from '@angular/material';

export interface UserData {
  TimeStamp: Date;
  Event: string;
}

@Component({
  selector: 'app-control-panel',
  templateUrl: './control-panel.component.html',
  styleUrls: ['./control-panel.component.css']
})
export class ControlPanelComponent implements OnInit, OnDestroy {
  
  ngOnDestroy() {
    clearTimeout(this.timeout);
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
    this.robot = null;
    this.robotService = null;
    this.robotSensor = null;
    this.timeout = setTimeout(()=>{ 
      window.dispatchEvent(new Event('resize'));
    },100);
  }

  private sub : Subscription;
  private sub1 : Subscription;
  private timeout;

  public type = "range";
  private service;

  private pval = 0.0;
  private vval = 0.0;
  private eval = 0.0;
  private sval = 0.0;
  private dval = 0.0;

  public limit = {
    poslim : { llim: 0, ulim:0}, 
    efflim: { llim: 0, ulim:0},
    vellim: { llim: 0, ulim:0}
  }

  private robotState = {
    joint_name: "",
    joint_id: 0,
    motor_position: 0,
    link_position: 0,
    motor_velocity: 0,
    link_velocity: 0,
    temperature: 0,
    effort: 0,
    stiffness: 0,
    damping: 0,
    fault_string: "",
    aux: 0,
    pos_ref: 0,
    vel_ref: 0,
    eff_ref: 0
  }

  private robotSensorFTState = {
    name: "",
    id: 0,
    forcex: 0,
    forcey: 0,
    forcez: 0,
    torquex: 0,
    torquey: 0,
    torquez: 0,
  }

  private robotSensorIMUState = {
    name: "",
    id: 0,
    ang_velx: 0,
    ang_vely: 0,
    ang_velz: 0,
    lin_accx: 0,
    lin_accy: 0,
    lin_accz: 0,
    orientationx: 0,
    orientationy: 0,
    orientationz: 0,
    orientationw: 0
  }

  public isJoint = true;
  public sensorType = "";
  private robotService: RobotStateService;
  private robot: Map<string, any>;
  private robotSensor: Map<string, any>;
  private isControlEnable = false;
  private isSorted = false;

  roundValue(item){
    
    for( var key of Object.keys(item)){
      if(typeof item[key] == "string") continue;
      item[key] = Math.round(item[key] * 100) / 100
    }
  }

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatTable) table: MatTable<any>;
  
  displayedColumns: string[] = ['TimeStamp','Empty', 'Event'];
  dataSource: MatTableDataSource<UserData>;

  constructor(robotService: RobotStateService, http: HttpClient) { 
    this.robotService = robotService;
    this.robot = new Map<string, any>();
    this.robotSensor = new Map<string, any>();
    this.service = new HttpService(http);
    this.service.setURL("/singlejoint");
    this.dataSource = new MatTableDataSource(this.getFaultMap());
    
    this.sub1 = this.robotService.currentJointmsg.subscribe(msg => {	
      this.setControl();
    });

    this.sub = this.robotService.currentmsg.subscribe(msg => {	
      this.robot = msg["robot"];
      this.robotSensor = msg["sensor"];     
      if(this.robot != null && this.robotService.isJoint){
        var item = this.robot.get(this.robotService.selectJointSensorName);
        if (item != null){
          if (item.id != this.robotState.joint_id)
            this.dataSource = new MatTableDataSource(this.getFaultMap());
          this.robotState = item;  
          //TODO fare evento al cambiamento del item selezionato e cambiare il datasource   
          //this.dataSource = new MatTableDataSource(this.getFaultMap());
          if (this.sort != null && !this.isSorted){
            this.sort.sort(<MatSortable>{
              id: 'TimeStamp',
              start: 'desc'
              }             
            );
            this.isSorted = true;
          }
          if (this.table!= null) {
            this.table.renderRows();
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;  
          }       
          this.roundValue(this.robotState);
          this.isJoint = true;
          var limit = this.robotService.limits.get(this.robotService.selectJointSensorName);
          if (limit != null)
            this.limit = limit;
        }
      }

      if (this.robotSensor!= null && !this.robotService.isJoint){
          var items = this.robotSensor.get(this.robotService.selectJointSensorName);
          if (items != null){
            this.sensorType = items.type;
            if (items.type == "ft"){
              this.robotSensorFTState = items;
              this.roundValue(this.robotSensorFTState);
            }
            else if (items.type == "imu"){
              this.robotSensorIMUState = items;
              this.roundValue(this.robotSensorIMUState);
            }
            this.isJoint = false;
          }
      }
    });
  }

  ngOnInit() {   
   
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  ngAfterViewInit(){

    window.dispatchEvent(new Event('resize'));
  }
  addPlot(id, topic, name){
    //this.robotService.changeView("Plotter");
    
    if (!this.robotService.plotterViewFlag){
      this.robotService.countView(true)
      this.robotService.plotterViewFlag = true;
      window.dispatchEvent(new Event('resize'));
    }
    this.timeout = setTimeout(()=>{ 
      
      window.dispatchEvent(new Event('resize'));
      this.robotService.addPlot(1,id,topic,name);
      
    },200);    
  }

  advertisePosition(name,value){
    var msg ={ "name":name, "value": value };
    this.robotService.advertiseCtrlSelectedJoint(msg);
  }

  setPosRef(param: number){

    this.pval = param;

  }

  setVelRef(param: number){
    
   this.vval = param;
    
  }

  setEffortRef(param: number){
    
   this.eval = param;
    
  }

  setStiffRef(param: number){
    
    this.sval = param;
    
  }

  setDampRef(param: number){
    
    this.dval = param;
    
  }

  sendVal(id:number){

    //{"joint":[{"id": 15, "val": 0},{"id": 16, "val": 0}]}
    this.service.create({"joint":[{"id": Number(this.robotState.joint_id), "pos": Number(this.pval),
    "vel": Number(this.vval),  "eff": Number(this.eval),  "stiff": Number(this.sval),
    "damp": Number(this.dval) }]})
    .subscribe(
      response => {
        
      },
      (error: AppError) => {

        //rimovo dal vettore
        
        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
       });

  }

  setControl(){
    
    if(this.isControlEnable){
      this.pval = this.robotState.pos_ref;
      this.vval = this.robotState.vel_ref;
      this.eval = this.robotState.eff_ref;
      this.sval = this.robotState.stiffness;
      this.dval = this.robotState.damping;
    }else {
      this.advertisePosition(null,0);
    }
  }

  getFaultMap(){
    var item = this.robotService.faultMap.get(this.robotService.selectJointSensorName);
    if (item == null) item = new Array();
    return item;
  }
}
