import { RobotStateService } from './../services/robot-state.service';
import { MAT_DIALOG_DATA, MatDialogRef, MatSortable, MatTable } from '@angular/material';
import { Inject } from '@angular/core';
import {Component, OnInit, ViewChild} from '@angular/core';
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import { Subscription } from 'rxjs';

export interface UserData {
  TimeStamp: Date;
  Event: string;
}

@Component({
  selector: 'app-logger',
  templateUrl: './logger.component.html',
  styleUrls: ['./logger.component.css']
})
export class LoggerComponent implements OnInit {
  displayedColumns: string[] = ['TimeStamp', 'JointId', 'JointName', 'Event', 'Action'];
  dataSource: MatTableDataSource<UserData>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatTable) table: MatTable<any>;

  private sub : Subscription;

  constructor(public dialogRef: MatDialogRef<LoggerComponent>,
              public robotService: RobotStateService) {
                
    this.dataSource = new MatTableDataSource(this.robotService.faultList);
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngAfterViewInit(){
    this.sub = this.robotService.currentFaultAddmsg.subscribe(msg => {	      
      if (msg != null){
        var fault = msg["fault"];     
        if (fault){
          if (this.table!= null) {
            console.log("render");
            this.table.renderRows();
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort; 
          }   
        }
      }
    });
  }

  ngOnDestroy() {
    if (this.sub != null)   
      this.sub.unsubscribe();
    this.robotService = null;    
  }

  showControlPanel(name){    
    this.robotService.selectJointSensorName = name;
    if (this.robotService.controlPanelViewFlag) {      
      this.robotService.advertiseSelectedJoint(this.robotService.selectJointSensorName);
      this.onNoClick();
      return;
    }
    this.robotService.controlPanelViewFlag = true;
    this.robotService.countView(this.robotService.controlPanelViewFlag);   
    this.robotService.advertiseSelectedJoint(this.robotService.selectJointSensorName);
    this.onNoClick(); 
  }

  ngOnInit() {
    this.sort.sort(<MatSortable>{
      id: 'TimeStamp',
      start: 'desc'
      }
    );
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;    
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

}
