import { Component} from '@angular/core';
//import { MatTabChangeEvent } from '@angular/material';
import { RobotStateService } from './services/robot-state.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { LoggerComponent } from './logger/logger.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  

  //public tabIndex = 0;
  //public toggleGlobal = "Global";
  private sub : Subscription;
  private isLoggerOpened = false;
  private l;
  
  public robotService: RobotStateService;
  constructor(robotService: RobotStateService,public dialog: MatDialog ){
    this.robotService =  robotService;
    this.l = atob("aHR0cHM6Ly9naXRodWIuY29tL3BlcHBlcmlnL3dlYkdVSQ==");
    this.sub = this.robotService.currentFaultAddmsg.subscribe(msg => {	      
      if (msg != null){       
        var fault = msg["fault"];     
        if (fault && !this.isLoggerOpened){
          this.openDialog();
        }
      }
    });
  }

  openDialog(): void {
    //this.robotService.nBadgeLogger = 0;
    const dialogRef = this.dialog.open(LoggerComponent, {
      width: '70%',
      height: '80%',
      //data: {}
    });
    this.isLoggerOpened = dialogRef!= null ;

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.robotService.nBadgeLogger = 0;
      this.isLoggerOpened = false;
    });
  }
  

}

