import {FlatTreeControl} from '@angular/cdk/tree';
import {Component, Injectable, OnInit} from '@angular/core';
import {MatTreeFlatDataSource, MatTreeFlattener} from '@angular/material/tree';
import {BehaviorSubject, Observable, of as observableOf} from 'rxjs';
import { HttpService } from './../services/http.service';
import { HttpClient } from '@angular/common/http';
import { NotFoundError } from './../common/not-foud-error';
import { AppError } from './../common/app-error';
import { RobotStateService } from './../services/robot-state.service';
import { SelectionModel } from '@angular/cdk/collections';

export class DeviceNode {
  children: DeviceNode[];
  devicename: string;
  devicetype: string;
  type: any;
  isJoint: boolean;
  isChain: boolean;
}

/** Flat node with expandable and level information */
export class DeviceFlatNode {
  devicename: string;
  type: any;
  devicetype: string;
  isJoint: boolean;
  isChain: boolean;
  level: number;
  expandable: boolean;
}


@Injectable()
export class FileDatabase {
  dataChange: BehaviorSubject<DeviceNode[]> = new BehaviorSubject<DeviceNode[]>([]);

  get data(): DeviceNode[] { return this.dataChange.value; }

  private service;
  private dataResponse;
 
  constructor(http: HttpClient) {
    this.service = new HttpService(http);
    this.service.setURL("/state");
    this.initialize();
  }

  getChains(){
    this.service.get("/chains")
    .subscribe(
      response => {

        var robot = this.dataResponse["Robot"];
        var joints = robot["joint_name"];
        var jointsid = robot["joint_id"];
        var sensors = this.dataResponse["Sensors"];
        var fts = sensors["fts"];
        var ftid = fts["ft_id"];
        var ft = fts["ft_name"];
        var imus = sensors["imus"];
        var imu = imus["imu_name"];
        var imuid = imus["imu_id"];
        var obj = {
          "Robot":  response["Chains"],
          "Sensor": {"fts": ft,"imus": imu},        
      };
      var stringjson = JSON.stringify(obj);
      const dataObject = JSON.parse(stringjson);
      console.log(dataObject);
      const data = this.buildFileTree(dataObject, 0,false,{"ftid": ftid, "imuid": imuid},null);
      // Notify the change.
      this.dataChange.next(data);
      },
      (error: AppError) => {

        //rimovo dal vettore
        
        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
      });
    }

  getData(){

    this.service.get("/state")
    .subscribe(
      response => {
        this.dataResponse = response;
        this.getChains();
      },
      (error: AppError) => {

        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
        });

    }
  initialize() {
    // Parse the string to json object.
    this.getData();
  }

  buildFileTree(value: any, level: number, isJoint: boolean, ids: any, devtype: any): DeviceNode[] {
    let data: any[] = [];
    for (let k in value) {
      let v = value[k]; 
      var ldevtype = 0;  
      let node = new DeviceNode();
      if (k == "Robot"){ 
        ldevtype = 0;
      }else if (k == "Sensor"){ 
        ldevtype = 1;
      }else if (k == 'imus'){
        ldevtype = 2;
      }else if (k == 'fts'){
        ldevtype = 3;
      }
      if (k == "Robot" || k == "Sensor" || k == 'imus' || k == 'fts'){
        node.devicename = `${k}`;   
        node.children = this.buildFileTree(v, level + 1, false,ids, ldevtype);  
      } else if (typeof v === 'object') {
        var chain = v["Chain"];
        if (chain != null){
          node.devicename = `${chain}`;   
          v = v["Val"];    
          node.isChain = true; 
          node.children = this.buildFileTree(v, level + 1, true,ids, ldevtype);  
        }else{
          if(isJoint){
            var jname = v["Name"]; 
            node.type = jname;
            node.isJoint = true;
            node.devicename = v["ID"];           
          }
      }
     }else{      
      node.type = v;
      node.isJoint = false;
      if (devtype == 2)
        node.devicename = ids["imuid"][k];
      if (devtype == 3)
        node.devicename = ids["ftid"][k];
      }
      data.push(node);
    }
    return data;
  }
}

@Component({
  selector: 'app-tree-panel',
  templateUrl: './tree-panel.component.html',
  styleUrls: ['./tree-panel.component.css'],
  providers: [FileDatabase]
})
export class TreePanelComponent implements OnInit {

  public selectedDevice="No Item Selected";
  public jointId;
  public isJoint= true;
  private timeout;
  public fault = 0;

  treeControl: FlatTreeControl<DeviceFlatNode>;
  treeFlattener: MatTreeFlattener<DeviceNode, DeviceFlatNode>;
  dataSource: MatTreeFlatDataSource<DeviceNode, DeviceFlatNode>;
  private robotService: RobotStateService;

  ngOnInit(){
  
    //console.log(this.dataSource);
  }

  setActiveDevice(param, param1){
    if( param == null) return;

    this.selectedDevice = param;
    //this.isJoint = this.robotService.isJoint;
    this.robotService.selectJointSensorName = param;   
    if(param1 != null) {  
      this.robotService.isJoint = param1;
      this.isJoint = this.robotService.isJoint;
    }
    if(param1 != null && param1){
      var isLastFault = false;
      this.jointId = this.robotService.getJointId(param);      
      this.robotService.selectJointSensorId = this.jointId;
      this.robotService.selectJointSensorName = param; 
      var lastFault = this.robotService.LastFaultMap.get(this.selectedDevice);
      if (lastFault!= null && lastFault != "" && currFault != null && currFault == ""){
        this.fault = 3;
        isLastFault = true;
      }
      var currFault = this.robotService.getCurrentFault(this.selectedDevice);
      if ( currFault != null && currFault != ""){
        this.fault = 2;
      }
      if ( currFault != null && currFault == "" && !isLastFault){
        this.fault = 1;
      }     
    }
    if (param1 != null && !param1){
      this.jointId = this.robotService.getSensorId(param);
      this.fault = 0;
    }
  }

 /* plotState(id, name){
    if(this.isJoint){
      this.robotService.changeView("AllPlots"); 
      this.timeout = setTimeout(()=>{ 
        this.robotService.plotState(this.robotService.selectJointSensorId,this.robotService.selectJointSensorName);
      },200);  
    }
  }*/

  constructor(database: FileDatabase,robotService: RobotStateService) {
    this.robotService = robotService;
    this.robotService.currentJointmsg.subscribe(msg => {
      	
      if(msg == null)return;
      if (Object.keys(msg).length < 1) return;      
      this.selectedDevice = msg;
      this.isJoint = this.robotService.isJoint;
      this.jointId = this.robotService.getJointId(this.selectedDevice);
      if (!this.isJoint){
       this.jointId = this.robotService.getSensorId(this.selectedDevice);
       this.fault = 0;
      }
      
      if (this.isJoint){
        var isLastFault = false;
        var currFault = this.robotService.getCurrentFault(this.selectedDevice);
        var lastFault = this.robotService.LastFaultMap.get(this.selectedDevice);
        if (lastFault!= null && lastFault != "" && currFault != null && currFault == ""){
          this.fault = 3;
          isLastFault = true;
        }
        if ( currFault != null && currFault != ""){
          this.fault = 2;
        }
        if ( currFault != null && currFault == "" && !isLastFault){
          this.fault = 1;
        }        
      }
    });

    this.treeFlattener = new MatTreeFlattener(this.transformer, this._getLevel,
      this._isExpandable, this._getChildren);
    this.treeControl = new FlatTreeControl<DeviceFlatNode>(this._getLevel, this._isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

    database.dataChange.subscribe(data => {
      this.dataSource.data = data;
      this.robotService.checklistSelection = new SelectionModel<any>(true, this.treeControl.dataNodes);
    });
  }

  transformer = (node: DeviceNode, level: number) => {
    let flatNode = new DeviceFlatNode();    
    flatNode.devicename = node.devicename;
    //console.log(node);
    flatNode.isChain = node.isChain;
    flatNode.type = node.type;
    flatNode.level = level;
    flatNode.isJoint = node.isJoint;
    flatNode.expandable = !!node.children;
    return flatNode;
  }

  private _getLevel = (node: DeviceFlatNode) => { return node.level; };

  private _isExpandable = (node: DeviceFlatNode) => { return node.expandable; };

  private _getChildren = (node: DeviceNode): Observable<DeviceNode[]> => {
    return observableOf(node.children);
  }

  hasChild = (_: number, _nodeData: DeviceFlatNode) => { return _nodeData.expandable; };

  itemSelectionToggle(node: DeviceFlatNode): void {
    this.robotService.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.robotService.checklistSelection.isSelected(node)
      ? this.robotService.checklistSelection.select(...descendants)
      : this.robotService.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants.every(child =>
      this.robotService.checklistSelection.isSelected(child)
    );
    this.checkAllParentsSelection(node);
  }

  leafItemSelectionToggle(node: DeviceFlatNode): void {
    this.robotService.checklistSelection.toggle(node);
    this.checkAllParentsSelection(node);
    //console.log(this.robotService.checklistSelection.selected);
  }

  descendantsAllSelected(node: DeviceFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.robotService.checklistSelection.isSelected(child)
    );
    return descAllSelected;
  }

  descendantsPartiallySelected(node: DeviceFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.robotService.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  checkAllParentsSelection(node: DeviceFlatNode): void {
    let parent: DeviceFlatNode | null = this.getParentNode(node);
    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  checkRootNodeSelection(node: DeviceFlatNode): void {
    const nodeSelected = this.robotService.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.robotService.checklistSelection.isSelected(child)
    );
    if (nodeSelected && !descAllSelected) {
      this.robotService.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.robotService.checklistSelection.select(node);
    }
  }

  getParentNode(node: DeviceFlatNode): DeviceFlatNode | null {
    const currentLevel = this._getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this._getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }
}